.main {
  height: calc(100vh - #{60px});
  display: grid;
  grid-template-areas: 'sidebar content';
  grid-template-columns: 25% 1fr;
}

.companies__container {
  padding: 20px 25px;
  grid-area: sidebar;
  border-right: 2px solid #e8e8e8;
  overflow: auto;
  z-index: 5;
  transition: all 0.3s;
}

.companies__List {
  padding: 0;
  margin: 0;
  list-style: none;

  a {
    color: #333;
  }
}

.companies__item {
  margin-bottom: 5px;
  letter-spacing: 0.5px;

  &:hover {
    background-color: rgb(216, 221, 221);
    border-radius: 2px;
  }
}
